import { IComponent } from '@/../models';
import { EPageStep, EPageAction } from '@/../enums';

export namespace ModelsSchedule {
    export enum EWeekday {
        sunday = 0,
        monday = 1,
        tuesday = 2,
        wednesday = 3,
        thursday = 4,
        friday = 5,
        saturday = 6,
    }

    export interface IPageItem {
        page: EPageStep;
        action: EPageAction;
    }

    export interface IServerResponseData {
        objectId: string;
        name: string;
        ranges: IScheduleRange[];
    }

    export interface ITableData {
        objectId: string;
        name: string;
        ranges: IScheduleRange[];
    }

    export interface ITableApiParam {
        paging: {
            page: number;
            pageSize: number;
        };
        sorting?: {
            field?: string;
            order?: number;
        };
        keyword?: string;
    }

    export interface IScheduleRange {
        startDate: string;
        endDate: string;
    }

    export interface IScheduleDate {
        startDate: Date;
        endDate: Date;
    }

    export interface ISelectionWeek {
        objectId: EWeekday;
        name: string;
    }

    export interface IFormItem {
        isClearState: boolean;
        timeFormat: string;

        week: ISelectionWeek;
        startTime: Date;
        endTime: Date;
        isAllDay: boolean;
    }

    export interface IFormData {
        objectId: string;
        name: string;
        week: ISelectionWeek[];
        startTime: Date[];
        endTime: Date[];
        isAllDay: boolean[];
    }

    export interface IFormConfigs {
        name: IComponent.IFormConfig;
        schedules: IComponent.IFormConfig;
    }

    export interface ISelectOptionItem {
        weeks: ISelectionWeek[];
    }

    export interface IFormDataDisabled {}

    export interface IFormHidden {}

    export interface IFormButtonDisabled {
        name: boolean;
    }
}
