import { AxiosResponse } from 'axios';

import { EFormType, EFormMode, ETableSorting, ETableRowType } from '@/../enums';
import { EPageStep, EPageAction } from '@/../enums';

import {
    ServiceRegex,
    ServiceResponseFilter,
    ServiceDialog,
    ServiceToast,
    ServiceServerConfig,
    ServiceFormConfig,
    EUserRole,
    ServiceBase64,
    WelcomeBackgroundDefaultBase64,
    ServiceTypeof,
    ServiceDatetime,
} from '@/../helpers';
import { IComponent } from '@/../models';

import { ServerApiPath, WebPagePath } from '@/config';
import { ModelsSchedule as Model } from './models';

// vue-multiselect
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';

// DatePicker
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';

// toggle-button
import { ToggleButton } from 'vue-js-toggle-button';

import Table from '@/../components/table/table.vue';
import Forms from '@/../components/form/forms.vue';
import Card from '@/../components/card/card.vue';
import Loading from '@/../components/loading/loading.vue';
import { FakeData } from '@/helpers/fake-data';

@Component({
    components: { Table, Card, Forms, Multiselect, DatePicker, ToggleButton },
})
export default class VuePageClass extends Vue {
    //#region Variables
    private formItem: Model.IFormItem = {
        isClearState: false,
        timeFormat: 'HH:mm',

        week: {
            objectId: Model.EWeekday.monday,
            name: this.$i18n.Date_Monday,
        },
        startTime: new Date('2000/01/02 00:00:00'),
        endTime: new Date('2000/01/02 23:59:59'),
        isAllDay: false,
    };

    private formDataOriginal: Model.IFormData = {
        objectId: '',
        name: '',
        week: [],
        startTime: [],
        endTime: [],
        isAllDay: [],
    };
    private formData: Model.IFormData = { ...this.formDataOriginal };

    private formConfig: Model.IFormConfigs = {
        name: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        schedules: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
    };
    private formConfigs: IComponent.IFormConfig[] = [];

    private selectOptionItem: Model.ISelectOptionItem = {
        weeks: [],
    };

    private formHidden: Model.IFormHidden = {};

    private formButtonDisabled: Model.IFormButtonDisabled = {
        name: true,
    };

    private eFormMode = EFormMode;
    private loading = new Loading();

    //#endregion

    //#region Computed form
    private get formSaveDisable(): boolean {
        let disabled: Model.IFormButtonDisabled = this.formButtonDisabled;

        if (disabled.name || this.formData.week.length <= 0) {
            return true;
        }
        return false;
    }
    //#endregion

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {
        await this.initSelectionItem();
        await this.initData(SCHEDULE);
    }
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion

    //#region Init Form Data
    private async initData(rowData: Model.ITableData): Promise<void> {
        this.initFormConfig();
        await this.formConfigSet(rowData);
    }

    private initFormConfig(): void {
        this.formConfigs = [];

        // name
        this.formConfig.name.id = 'name';
        this.formConfig.name.label = this.$i18n.Common_Name;
        this.formConfig.name.placeholder = this.$i18n.Common_Name;
        this.formConfig.name.formType = EFormType.text;
        this.formConfig.name.required = true;
        this.formConfig.name.cols = 11;

        // schedules
        this.formConfig.schedules.id = 'schedules';
        this.formConfig.schedules.formType = EFormType.slot;

        this.formConfigs = [this.formConfig.name, this.formConfig.schedules];
    }
    //#endregion

    //#region Init Select Option
    private initSelectionItem(): void {
        this.selectOptionItem.weeks = [
            { objectId: Model.EWeekday.sunday, name: this.$i18n.Date_Sunday },
            { objectId: Model.EWeekday.monday, name: this.$i18n.Date_Monday },
            { objectId: Model.EWeekday.tuesday, name: this.$i18n.Date_Tuesday },
            { objectId: Model.EWeekday.wednesday, name: this.$i18n.Date_Wednesday },
            { objectId: Model.EWeekday.thursday, name: this.$i18n.Date_Thursday },
            { objectId: Model.EWeekday.friday, name: this.$i18n.Date_Friday },
            { objectId: Model.EWeekday.saturday, name: this.$i18n.Date_Saturday },
        ];
    }
    //#endregion

    //#region Form
    private formTitle(title: string): string {
        return title ? title : this.$i18n.Create_Schedule;
    }

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
        this.formData.week = [];
        this.formData.startTime = [];
        this.formData.endTime = [];
        this.formData.isAllDay = [];

        this.formItem.isClearState = false;

        this.initAddSchedules();
    }

    private formConfigValues(value: any, disabledButton: boolean, id: string): void {
        if (this.formData.hasOwnProperty(id)) {
            this.formData[id] = value;
        }

        if (this.formButtonDisabled.hasOwnProperty(id)) {
            this.formButtonDisabled[id] = disabledButton;
        }
    }

    private async formConfigSet(data: any = undefined): Promise<void> {
        if (data) await this.formDataSet(data);
        this.formHiddenValues();
    }

    private formHiddenValues(): void {}

    private async formDataSet(data: Model.ITableData): Promise<void> {
        this.formData.objectId = data.objectId ?? data.objectId;
        this.formData.name = data.name ?? data.name;
        this.resolveWeek(data.ranges);
        this.resolveTime(data.ranges);

        this.formItem.isClearState = false;
    }

    //#region Add Schedules
    private formAddSchedules() {
        this.formData.week.push(this.formItem.week);
        this.formData.startTime.push(this.formItem.startTime);
        this.formData.endTime.push(this.formItem.endTime);
        this.formData.isAllDay.push(this.formItem.isAllDay);

        this.initAddSchedules();
    }

    private formRemoveSchedules(index: number): void {
        this.formData.week.splice(index, 1);
        this.formData.startTime.splice(index, 1);
        this.formData.endTime.splice(index, 1);
        this.formData.isAllDay.splice(index, 1);
    }

    private initAddSchedules(): void {
        this.formItem.week = {
            objectId: Model.EWeekday.monday,
            name: this.$i18n.Date_Monday,
        };
        this.formItem.startTime = new Date('2000/01/02 00:00:00');
        this.formItem.endTime = new Date('2000/01/02 23:59:59');
        this.formItem.isAllDay = false;
    }
    //#endregion

    //#endregion

    //#region Server API (save, delete, test)
    private async dataSave(): Promise<void> {
        let data: Model.IFormData = this.formData;

        let tempSchedules: Model.IScheduleRange[] = this.resolveSchedules();

        if (tempSchedules.length === 0) {
            return;
        }

        for (let index in tempSchedules) {
            if (tempSchedules[index].startDate && !isNaN(new Date(tempSchedules[index].startDate).getTime())) {
                let startDate = new Date(tempSchedules[index].startDate);
                startDate.setSeconds(0);
                startDate.setMilliseconds(0);
                tempSchedules[index].startDate = startDate.toISOString();
            }

            // Determine the value of enddate according to whether it is the same day
            let tempStartDate: string = ServiceDatetime.datetime2String(new Date(tempSchedules[index].startDate), 'D');
            let tempEndDate: string = ServiceDatetime.datetime2String(new Date(tempSchedules[index].endDate), 'D');

            if (parseInt(tempEndDate, 10) - parseInt(tempStartDate, 10) === 0) {
                if (tempSchedules[index].endDate && !isNaN(new Date(tempSchedules[index].endDate).getTime())) {
                    let endDate = new Date(tempSchedules[index].endDate);
                    endDate.setMinutes(endDate.getMinutes() - 1);
                    endDate.setSeconds(59);
                    endDate.setMilliseconds(999);
                    tempSchedules[index].endDate = endDate.toISOString();
                }
            } else if (parseInt(tempEndDate, 10) - parseInt(tempStartDate, 10) === 1) {
                let endDate = new Date(tempSchedules[index].endDate);
                tempSchedules[index].endDate = endDate.toISOString();
            }
        }

        let dataParam: {
            objectId?: string;
            name: string;
            ranges: Model.IScheduleRange[];
        } = {
            name: data.name,
            ranges: tempSchedules,
        };

        if (data.objectId) {
            dataParam.objectId = data.objectId;
        }

        const param = { datas: [dataParam] };

        this.loading.show();

        let target = param.datas[0];

        SCHEDULE = {
            objectId: target.objectId,
            name: target.name,
            ranges: target.ranges,
        };

        this.loading.hide();

        ServiceToast.success(this.$i18n.Dialog_SaveSuccess);
    }

    //#endregion

    //#region Resolve text
    private resolveSchedules(): Model.IScheduleRange[] {
        let result: Model.IScheduleRange[] = [];
        let resultDate: Model.IScheduleDate[] = [];
        let tempResult = null;
        let isOverlap: any = null;

        // 
        let tempAllDate = null;
        let tempStartDate = null;
        let tempEndDate = null;

        for (let i in this.formData.week) {
            let tempWeek = this.formData.week[i].objectId;
            let tempStartTime = this.formData.startTime[i];
            let tempEndTime = this.formData.endTime[i];
            if (this.formData.isAllDay[i]) {
                tempAllDate = ServiceDatetime.normalizeDayAndTime(tempWeek, tempStartTime, tempEndTime);
                tempStartDate = tempAllDate.startDate;
                tempEndDate = ServiceDatetime.dateStart(ServiceDatetime.dateByStep(tempAllDate.startDate, 1));
                tempResult = {
                    startDate: tempStartDate,
                    endDate: tempEndDate,
                };
            } else {
                tempResult = ServiceDatetime.normalizeDayAndTime(tempWeek, tempStartTime, tempEndTime);
            }

            resultDate.push(tempResult);
        }

        isOverlap = ServiceDatetime.checkRangesOverlapAll(resultDate);
        let overlapError: string = `${this.$i18n.Schedule_Schedule_Overlap} <br>`;

        if (isOverlap.isOverlap) {
            if (ServiceTypeof.isArray(isOverlap.results)) {
                isOverlap.results.forEach((item) => {
                    if (item.isOverlap) {
                        overlapError += `No.${item.now + 1} And No.${item.overlap + 1} ${this.$i18n.Schedule_Schedule_Overlap} <br>`;
                    }
                });
            }

            let title: string = this.$i18n.Status_Error;
            ServiceDialog.ErrorConfirmOnly(this, overlapError, title);
        } else {
            if (ServiceTypeof.isArray(resultDate)) {
                resultDate.forEach((item) => {
                    let value: any = item;
                    let date: Model.IScheduleRange = {
                        startDate: value.startDate.toISOString(),
                        endDate: value.endDate.toISOString(),
                    };
                    result.push(date);
                });
            }
        }
        return result;
    }

    private resolveWeek(schedules: Model.IScheduleRange[]) {
        this.formData.week = [];
        if (ServiceTypeof.isArray(schedules)) {
            schedules.forEach((schedule, scheduleIndex) => {
                let tempStartDate: string = ServiceDatetime.datetime2String(new Date(schedule.startDate), 'D');
                let tempEndDate: string = ServiceDatetime.datetime2String(new Date(schedule.endDate), 'D');

                if (parseInt(tempEndDate, 10) - parseInt(tempStartDate, 10) === 0) {
                    this.formData.isAllDay.push(false);
                } else if (parseInt(tempEndDate, 10) - parseInt(tempStartDate, 10) === 1) {
                    this.formData.isAllDay.push(true);
                }

                this.selectOptionItem.weeks.forEach((week) => {
                    if (new Date(schedule.startDate).getDay() === week.objectId) {
                        this.formData.week.push(week);
                    }
                });
            });
        }
    }

    private resolveTime(schedules: Model.IScheduleRange[]) {
        this.formData.startTime = [];
        this.formData.endTime = [];
        if (ServiceTypeof.isArray(schedules)) {
            schedules.forEach((schedule) => {
                this.formData.startTime.push(new Date(schedule.startDate));
                // endTime 
                let tempResult = new Date(schedule.endDate);
                tempResult.setSeconds(tempResult.getSeconds() + 1);
                this.formData.endTime.push(tempResult);
            });
        }
    }

    private resolveDisplayTime(date: string | Date) {
        let newDate = new Date(date);
        return ServiceDatetime.toString(newDate, this.formItem.timeFormat);
    }
    //#endregion
}

